.HomeButton {
  width: 40px;
  height: 40px;

  font-size: 1.4rem;
  line-height: 1rem;

  color: var(--app-text-base-color);

  border: 0;

  background-color: var(--app-background-5-darker-color);

  cursor: pointer;

  outline: 0;
}

.HomeButton:hover,
.HomeButton:focus {
  background-color: var(--app-background-10-darker-color);
}
